._ellipse{
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 20em;
  width: 81.25em;
  height: 34.25306em;
  border-radius: 81.25em;
  opacity: 0.4;
  background: $primary;
  filter: blur(310px);
  max-width: 100%;
  pointer-events: none;
}

.faq{
  padding: 6.25em 0 7.38em;
  position: relative;
  .h1{
    text-align: center;
  }
  &__items{
    margin: 5.125em auto 0;
    width: 67.375em;
    max-width: 100%;
    @include grid100gap(3.75em);
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 6.25em;
  }
  &__btn{
    width: 58.75em;
    max-width: 100%;
    &:after{
      width: 24.75em;
      max-width: 100%;
      height: 100%;
    }
  }
}

.qa-card{
  padding-bottom: 1.625em;
  position: relative;
  &._active{
    .qa-card__btn{
      img{
        transform: rotate(-180deg);
      }
    }
  }
  &:before{
    content: '';
    position: absolute;
    bottom: 0;
    right: 0;
    width: calc(100% - 5.125em);
    height: 1px;
    background: linear-gradient(to right,#0ff ,rgba(#0ff,.5) 51.87%,rgba(#0ff,.0) 100%);
  }
  &__show{
    display: flex;
    cursor: pointer;
  }
  &__btn{
    width: 3.25em;
    height: 3.25em;
    flex-shrink: 0;
    margin-right: 1.875em;
    position: relative;
    &:before{
      content: '';
      @include centerAbsolute;
      background: #B2FF51;
      opacity: 0.5;
      filter: blur(13.893758773803711px);
      z-index: -1;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      transition: inherit;
    }
    img{
      width: 100%;
      display: block;
      transition: all .3s;
    }
    &:hover{
      &:before{
        transform: translate(-50%,-50%) scale(1.1);
      }
    }
  }
  &__title span,
  &__desc{
    font-size: 1.625em;
    line-height: 115%;
  }
  &__title{
    padding-top: .56em;
    font-weight: 700;
    color: $primary;
  }
  &__hidden{
    padding: 1.625em 0 0 5.125em;
    display: none;
  }
}

.page-bg{
  position: absolute;
  right: 0;
  bottom: -120em;
  width: 40.28em;
}

.you-learn{
  padding: 6.25em 0;
  .h1{
    text-align: center;
  }
  &__items{
    margin-top: 5.19em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 2.25em / 4));
    gap: .75em;
  }
}

.learn-card{
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.07) 0%, rgba(15, 18, 47, 0.50) 100%), #6141D4;
  height: 22.5em;
  border-radius: 1.375em;
  padding: 1.375em 2em 2em;
  &:before{
    background: rgba(217, 217, 217, 0.10);
  }
  &__icon{
    position: relative;
    z-index: 2;
    height: 12em;
    width: 12em;
    display: flex;
    align-items: flex-end;
    margin-bottom: 1.625em;
    img{
      max-height: 100%;
      max-width: 100%;
    }
  }
  &__title{
    font-size: 1.5em;
    line-height: 115%;
    span{
      font-weight: 600;
    }
  }
}


@media screen and (max-width: 1588px){
  .faq{
    padding: 3em 0 2em;
    .h1{
      span{
        display: block;
      }
    }
    &__items{
      width: 50em;
      margin-top: 4.06em;
    }
    &__nav{
      margin-top: 4.5em;
    }
    &__btn{
      width: 50em;
    }
  }

  .qa-card{
    br{
      display: none;
    }
  }

  .you-learn{
    padding: 3em 0;
    &__items{
      margin-top: 4em;
      grid-template-columns: repeat(4,calc(25% - 3em / 4));
      gap: 1em;
    }
  }

  .learn-card{
    padding: 2.375em 1.5em;
    height: 17.5625em;
    border-radius: 1em;
    &__icon{
      width: 7.5em;
      height: 7.5em;
    }
    &__title{
      font-size: 1.25em;
    }
  }
}


@media screen and (max-width: 1280px){
  .faq{
    padding-bottom: 4.25em;
    &__items{
      margin-top: 3em;
      width: 44.875em;
      gap: 3.06em;
    }
    &__btn{
      width: 44.875em;
    }
  }

  ._ellipse{
    width: 36.875em;
    height: 15.5625em;
    top: 3em;
  }

  .you-learn{
    &__items{
      margin-top: 3em;
      grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
    }
  }
}


@media screen and (max-width: 992px){
  .faq{
    padding: 2.25em 0;
    &__items{
      margin-top: 2.5em;
      width: 36.25em;
      gap: 1.5em;
    }
    &__nav{
      margin-top: 2.81em;
    }
    &__btn{
      width: 36.25em;
      height: 5em;
      border-radius: 0.625em;
      span{
        font-size: 1.5em;
      }
    }
  }

  .qa-card{
    padding-bottom: 1.5em;
    &:before{
      width: calc(100% - 3.44em);
    }
    &__btn{
      width: 2em;
      margin-right: 1.44em;
    }
    &__title span,
    &__desc{
      font-size: 1.125em;
    }
    &__hidden{
      padding: 1em 0 0 3.44em;
    }
  }

  .you-learn{
    padding: 2.25em 0;
    &__items{
      grid-template-columns: repeat(4,calc(25% - 2.25em / 4));
      gap: .75em;
    }
  }

  .learn-card{
    padding: .625em .5625em;
    height: 10em;
    border-radius: .625em;
    &__icon{
      width: 5.25em;
      height: 3.25em;
      margin-bottom: .44em;
    }
    &__title{
      font-size: .875em;
    }
  }
}


@media screen and (max-width: 576px){
  .faq{
    &__items{
      margin-top: 2em;
    }
    &__nav{
      margin-top: 3em;
    }
    &__btn{
      width: 15.75em;
      height: 3.625em;
      span{
        font-size: 1em;
      }
    }
  }

  .qa-card{
    padding-bottom: 1em;
  }

  .you-learn{
    &__items{
      margin-top: 2em;
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
    }
  }
}
