.pluses{
  padding: 6.25em 0;
  position: relative;
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    display: block;
    pointer-events: none;
    z-index: -1;
  }
  .container{
    position: relative;
    z-index: 2;
  }
  &__items{
    margin: 5em auto 0;
    width: 66em;
    max-width: 100%;
    @include grid100gap(2em);
  }
}

.plus-item{
  display: flex;
  &__icon{
    margin-right: 2em;
  }
  &__text{
    font-size: 1.625em;
    line-height: 115%;
    span{
      font-weight: 700;
      color: $primary;
    }
  }
}

.problems{
  position: relative;
  padding: 6.25em 0;
  .h1{
    font-size: 5em;
    max-width: 15em;
    margin: 0 auto;
  }
  .faq__nav{
    margin-top: 4.5em;
  }
  .faq__btn{
    max-width: 48.625em;
  }
}

.how-study{
  padding: 6.25em 0;
  position: relative;
  &__bg{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    display: block;
    z-index: -1;
    pointer-events: none  ;
  }
  ._ellipse{
    background: #465DB3;
    filter: blur(314.20001220703125px);
  }
  &__grid{
    position: relative;
    margin-top: 5.125em;
    width: 86em;
    max-width: 100%;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 7.875em / 3));
    gap: 5.81em 3.9375em;
  }
  &__arrow{
    position: absolute;
    top: 9.25em;
    left: 0;
    width: calc(100% + 5.06em);
    img{
      width: 100%;
      display: block;
    }
    ._view2,
    ._view3,
    ._view4,
    ._view5{
      display: none;
    }
  }
  @media screen and (min-width: 1589px){
    .study-card{
      &:nth-child(4),
      &:nth-child(5),
      &:nth-child(6){
        transform: translateX(4.94em);
      }
    }
  }
}

.study-card{
  padding: 1em;
  min-height: 24.5625em;
  @include center;
  flex-direction: column;
  text-align: center;
  background: rgba(217, 217, 217, 0.11);
  border: 1px solid rgba(#fff,.3);
  border-radius: 1.375em;
  position: relative;
  &:nth-child(3n){
    @media screen and (min-width: 577px){
      .study-card__arrow{
        display: none;
      }
    }
  }
  &__arrow{
    transform: translateY(-50%);
    position: absolute;
    top: 9.38em;
    right: -1.875em;
    width: 3.875em;
    img{
      width: 100%;
      display: block;
    }
    ._view2,
    ._view3{
      display: none;
    }
  }
  &__icon{
    margin-bottom: 2.75em;
    width: 11em;
    max-height: 9.25em;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__title{
    font-size: 1.5em;
    line-height: 115%;
    font-weight: 700;
    span:not(.study-card__title-hide-mob){
      color: $primary;
    }
  }
}

.payment{
  padding: 6.25em 0;
  &__inner{
    margin: 0 auto;
    width: 92.5em;
    max-width: 100%;
    background: rgba(217, 217, 217, 0.11);
    border-radius: 1.375em;
    overflow: hidden;
    display: grid;
    grid-template-columns: calc(100% - 36.125em) 36.125em;
  }
  &__col1{
    padding: 4.25em 2em 5em 4.75em;
  }
  &__nav{
    display: flex;
    margin-top: 4.75em;
    max-width: 45.8125em;
  }
  &__btn{
    width: 100%;
    border: 1px solid #fff;
    &:after{
      display: none;
    }
  }
  &__col2{
    border-radius: 1.375em 0 0 0;
    background-color: rgba($primary,.2);
    padding: 4.75em 4.75em 3.875em 3.625em;
  }
  &__list{
    @include grid100gap(2em);
  }
}

.payment-item{
  display: flex;
  &__icon{
    margin-right: 1.25em;
  }
  &__text{
    font-size: 1.625em;
    line-height: 115%;
    span{
      font-weight: 700;
      color: $primary;
    }
  }
}


@media screen and (max-width: 1588px){
  .pluses{
    padding: 3em 0;
    &__items{
      width: 50em;
    }
  }

  .plus-item{
    &__icon{
      margin-right: 1em;
    }
    &__text{
      font-size: 1.5em;
    }
  }

  .problems{
    padding: 4.5em 0;
    .h1{
      span{
        display: block;
      }
    }
    .faq__nav{
      margin-top: 3.5em;
    }
  }

  .how-study{
    padding: 3em 0;
    &__grid{
      margin-top: 4em;
      width: 72.5em;
    }
    &__items{
      grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
      gap: 3.125em 1em;
    }
    &__arrow{
      width: calc(100% + .4375em);
      left: 1.06em;
      top: 3.5em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    @media screen and (min-width: 1281px){
      .study-card{
        &:nth-child(4),
        &:nth-child(5),
        &:nth-child(6){
          transform: translateX(2.44em);
        }
      }
    }
  }

  .study-card{
    min-height: 18.25em;
    &__icon{
      margin-bottom: 1.25em;
      font-size: .875em;
    }
    &__arrow{
      top: 3.5em;
    }
  }

  .payment{
    &__inner{
      width: 74.9375em;
      grid-template-columns: calc(100% - 32.4375em) 32.4375em;
    }
    &__col1{
      padding: 1.5em 2.5em 3.44em;
    }
    &__nav{
      margin-top: 1.5em;
      max-width: 36.5625em;
    }
    &__col2{
      padding: 3.625em 1.75em 4.375em 3.5em;
    }
  }

  .payment-item{
    &__icon{
      margin-right: 1em;
      width: 1.75em;
    }
    &__text{
      font-size: 1.5em;
    }
    br{
      display: none;
    }
  }
}


@media screen and (max-width: 1280px){
  .pluses{
    &__items{
      margin-top: 3em;
      width: 44.875em;
    }
  }

  .problems{
    .h1{
      font-size: 4.375em;
    }
    .faq__nav{
      margin-top: 2.375em;
    }
  }

  .how-study{
    &__grid{
      margin-top: 3em;
      width: 54.125em;
    }
    &__items{
      gap: 3em 1em;
    }
    &__arrow{
      width: calc(100% + 2em);
      left: 0;
      top: 4.1875em;
      ._view2{
        display: none;
      }
      ._view3{
        display: block;
      }
    }
    @media screen and (min-width: 993px){
      .study-card{
        &:nth-child(4),
        &:nth-child(5),
        &:nth-child(6){
          transform: translateX(2em);
        }
      }
    }
  }

  .study-card{
    min-height: 18.6875em;
    &__icon{
      margin-bottom: 2em;
      font-size: .75em;
    }
    &__arrow{
      top: 4.1875em;
    }
    &__title{
      font-size: 1.125em;
    }
  }

  .payment{
    &__inner{
      grid-template-columns: calc(100% - 24.25em) 24.25em;
    }
    &__col1{
      padding: 2.5em 2em 2.25em 2.5em;
    }
    &__nav{
      margin-top: 2.5em;
    }
    &__col2{
      padding: 3.75em 1.75em 2.75em 2em;
    }
  }

  .payment-item{
    &__icon{
      margin-right: .875em;
    }
  }
}


@media screen and (max-width: 992px){
  .pluses{
    padding: 2.25em 0;
    &__items{
      margin-top: 2em;
      width: 30.5625em;
    }
  }

  .plus-item{
    &__icon{
      width: 1.5em;
      height: 1.5em;
      &:before{
        width: 2.625em;
        height: 2.625em;
      }
    }
    &__text{
      font-size: 1.125em;
    }
  }

  .problems{
    padding: 3em 0;
    .h1{
      font-size: 3.125em;
    }
    .faq__nav{
      margin-top: 2.5em;
    }
    .faq__btn{
      max-width: 22.625em;
    }
  }

  .how-study{
    padding: 2.25em 0;
    &__grid{
      width: 43.125em;
    }
    &__items{
      grid-template-columns: repeat(3,calc(100% / 3 - .5em));
      gap: 2.5em .75em;
    }
    &__arrow{
      width: calc(100% + 1.875em);
      left: -.9375em;
      top: 2.875em;
      ._view3{
        display: none;
      }
      ._view4{
        display: block;
      }
    }
  }

  .study-card{
    min-height: 14em;
    border-radius: 1em;
    padding: 1.8em .5em .5em;
    justify-content: unset;
    &__icon{
      margin-bottom: .81em;
      font-size: 1em;
      width: 6.5em;
      height: 5.5em;
      max-height: unset;
    }
    &__arrow{
      top: 2.875em;
      width: 1.8125em;
      right: -.69em;
      ._view2{
        display: block;
      }
      ._view1{
        display: none;
      }
    }
    &__title{
      font-weight: 600;
      &-hide-mob{
        display: none;
      }
    }
  }

  .payment{
    padding: 3em 0;
    &__inner{
      grid-template-columns: 100%;
      padding-bottom: 8.44em;
      position: relative;
    }
    &__col1{
      padding: 1.5em .5em 1.44em 1.5em;
    }
    .h1{
      font-size: 3.5em;
    }
    &__nav{
      margin-top: 2.5em;
    }
    &__col2{
      padding: 1.25em 1.875em;
      border-radius: 0;
      border-top: 1px solid rgba($primary,.4);
    }
    &__list{
      gap: 1em;
    }
    &__nav{
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 8.44em;
      @include center;
      padding: 0 3.875em;
      max-width: unset;
    }
    &__btn{
      height: 3.625em;
      border-radius: 0.65em;
      span{
        font-size: 1em;
      }
    }
  }

  .payment-item{
    &__icon{
      margin-right: 1em;
      width: 1.5em;
      &:before{
        width: 2.625em;
        height: 2.625em;
      }
    }
    &__text{
      font-size: 1.125em;
    }
  }
}


@media screen and (max-width: 576px){
  .problems{
    padding: 2.25em 0;
    .h1{
      font-size: 2.5em;
    }
    .faq__nav{
      margin-top: 2em;
    }
    .faq__btn{
      max-width: 15.75em;
    }
  }

  .how-study{
    &__grid{
      width: 20.5em;
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - .25em));
      gap: 1.9375em .5em;
    }
    &__arrow{
      width: calc(100% + .44em);
      left: 0;
      top: 3.125em;
      ._view4{
        display: none;
      }
      ._view5{
        display: block;
      }
    }
    .study-card{
      &:nth-child(3),
      &:nth-child(4),
      &:nth-child(5),
      &:nth-child(6){
        transform: translateX(.44em);
      }
    }
  }

  .study-card{
    min-height: 10.125em;
    border-radius: .46em;
    padding: 1.13em .5em .25em;
    &:nth-child(2n){
      .study-card__arrow{
        display: none;
      }
    }
    &__icon{
      margin-bottom: .5em;
      width: 4.625em;
      height: 3.9375em;
    }
    &__arrow{
      top: 3.125em;
      width: 2.125em;
      right: -1.19em;
      ._view3{
        display: block;
      }
      ._view2{
        display: none;
      }
    }
    &__title{
      font-size: .875em;
    }
  }

  .payment{
    padding: 2.25em 0;
    &__inner{
      padding-bottom: 7.625em;
    }
    &__col1{
      padding: 1.5em 2em;
    }
    &__nav{
      padding: 0 1em;
      height: 7.625em;
    }
  }
}
