@import 'vars';
html {
    width: 100%;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    width: 100%;
}

.m-page {
    width: calc(100vw - (100vw - 100%));
    overflow-x: hidden;
    font-family: $font1;
    font-weight: 400;
    color: #FFF;
    background-color: $black;
    font-size: calc(100vw * (16 / 1920));
    background-image: url(../img/bg.png);
    background-repeat: no-repeat;
    background-size: 40.28em auto;
    background-position: right 0 bottom -120em;
    &.fixed-body,
    &.fixed-body2{
        overflow-y: hidden;
        padding-right: var(--scrollWidth);
    }
}

.container {
    width: 100%;
    margin: 0 auto;
    padding: 0 13.75em;
}

a {
    &:hover {
        color: $primary;
    }
}

@media screen and (max-width: 1588px){
    .m-page{
        font-size: calc(100vw * (16 / 1588));
        background-size: 32.5em auto;
        background-position: right bottom -70em;
    }

    .container{
        padding: 0 12.125em;
    }
}


@media screen and (max-width: 1280px){
    .m-page{
        font-size: calc(100vw * (16 / 1024));
        background-size: 17.5em auto;
        background-position: right bottom -110em;
    }

    .container{
        padding: 0 1.375em;
    }
}


@media screen and (max-width: 992px){
    .m-page{
        font-size: calc(100vw * (16 / 768));
        background-size: 24.0625em auto;
        background-position: right bottom -85em;
    }

    .container{
        padding: 0 1.5em;
    }
}


@media screen and (max-width: 576px){
    .m-page{
        font-size: calc(100vw * (16 / 365));
        background-size: 16.25em auto;
        background-position: right bottom -95em;
    }

    .container{
        padding: 0 .9375em;
    }
}


