.main{
  padding: 9.14em 0 0;
  position: relative;
  &__title{
    text-align: center;
    font-weight: 700;
    font-family: $font2;
    font-size: 11.75em;
    line-height: 115%;
    //color: #000211;
    color: rgba(#fff,.9);
    span{
      color: $primary;
    }
  }
  &__img{
    position: relative;
    z-index: 2;
    margin-top: -5.5em;
    img{
      display: block;
      width: 100%;
    }
    ._view2,
    ._view3,
    ._view4{
      display: none;
    }
  }
  &__bottom{
    position: absolute;
    width: 100%;
    left: 0;
    bottom: 11.75em;
    z-index: 3;
  }
  &__bg{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    img{
      width: 100%;
      display: block;
    }
  }
}

.main-offer{
  position: relative;
  padding: 1.94em 2.56em;
  border-radius: 1.375em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: inherit;
    border: 1px solid #FFF;
    background: radial-gradient(164.96% 127.84% at 50% -19.59%, rgba(0, 224, 255, 0.25) 0%, rgba(37, 130, 216, 0.00) 62.48%), linear-gradient(0deg, rgba(0, 2, 17, 0.49) 0%, rgba(0, 2, 17, 0.49) 100%), rgba(217, 217, 217, 0.11);
    backdrop-filter: blur(12.850000381469727px);
    z-index: -1;
  }
  &__info{
    @include grid100gap(.375em);
  }
  &__title{
    font-size: 2.625em;
    line-height: 115%;
    font-weight: 700;
  }
  &__desc{
    font-size: 1.625em;
    line-height: 115%;
  }
  &__btn{
    padding: 0 4em;
    flex-shrink: 0;
    margin-left: 2em;
  }
}

.for-who{
  padding-bottom: 6.125em;
  position: relative;
  &__wrap{
    overflow: hidden;
    position: relative;
    z-index: 2;
  }
  &__ellipse{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 9.5em;
    opacity: 0.4;
    filter: blur(310px);
    max-width: 100%;
  }
  .h1{
    max-width: 8em;
    text-align: center;
    margin: 0 auto;
  }
}

.who-slider{
  margin: 6.25em auto 0;
  width: 91.25em;
  max-width: 100%;
  position: relative;
  .swiper{
    &-container{
      width: calc(100% + 6.25em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      padding-right: 6.25em;
      height: auto;
    }
  }
}

.swiper-button{
  top: 50%;
  transform: translateY(-50%);
  margin: 0;
  width: 5.625em;
  height: 5.625em;
  @include center;
  border-radius: 50%;
  border: 1px solid $primary;
  background: radial-gradient(50% 50% at 50% 50%, rgba(3, 255, 255, 0.00) 0%, rgba(3, 255, 255, 0.20) 100%);
  backdrop-filter: blur(7.5px);
  transition: all .3s;
  svg{
    width: .875em;
    height: auto;
  }
  &:hover{
    backdrop-filter: blur(1px);
    opacity: .8;
  }
  &-prev{
    left: -4.31em;
  }
  &-next{
    right: -4.31em;
  }
}

.who-card{
  background: rgba(217, 217, 217, 0.11);
  height: 100%;
  display: grid;
  grid-template-columns: 50% 50%;
  border: 1px solid rgba(#fff,.09);
  border-radius: 1.375em;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    top: 10.375em;
    transform: translateX(-50%) rotate(-180deg);
    left: 50%;
    width: calc(100% - 6.25em);
    height: 1px;
    opacity: 0.4;
    background: linear-gradient(270deg, rgba(0, 255, 255, 0.00) -2.49%, #0FF 2.42%, rgba(0, 255, 255, 0.00) 51.87%);
  }
  &:after{
    content: '';
    position: absolute;
    top: 0;
    //transform: translateX(-50%) rotate(-180deg);
    left: 12.25em;
    width: 3px;
    height: 100%;
    opacity: 0.14;
    background: linear-gradient(to bottom, #0FF -2.49%, rgba(0, 255, 255, 0.00) 51.87%);
  }
  &__content{
    padding-left: 3.125em;
  }
  &__top{
    padding: 2.5em 0 2.125em;
  }
  &__num{
    color: $primary;
  }
  &__bottom{
    padding: 3em 4.25em 8em 8.75em;
  }
  &__info{
    margin-top: 6.875em;
  }
  &__desc{
    margin-top: .9375em;
    max-width: 10.5em;
  }
  &__content2{
    position: relative;
    height: 100%;
    .who-card__info{
      position: absolute;
      padding: 6em 6.5em;
      width: 100%;
      left: 0;
      bottom: 0;
      z-index: 2;
    }
    .who-card__desc{
      margin-top: .6em;
      margin-bottom: .14em;
    }
  }
  &__img{
    @include imgCover(98%);
    min-height: 100%;
    border-radius: 0 1.375em 1.375em 0;
  }
  &__desc2{
    margin-top: 3em;
  }
  &__title2{
    margin-top: .5em;
    span{
      display: block;
      color: $primary;
    }
  }
}


@media screen and (max-width: 1588px){
  .main{
    padding: 5.5em 0 0;
    &__bg{
      top: -6.25em;
    }
    &__title{
      font-size: 9.157em;
    }
    &__img{
      margin: -3em auto 0;
      max-width: 100%;
      width: 63.75em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__bottom{
      bottom: 0;
    }
  }

  .main-offer{
    padding: 1.19em 2em 1.56em;
    width: 60.3125em;
    max-width: 100%;
    margin: 0 auto;
    &__info{
      gap: .31em;
    }
    &__title{
      font-size: 2.5em;
    }
    &__desc{
      font-size: 1.25em;
    }
    &__btn{
      padding: 0 3.75em;
    }
  }

  .for-who{
    padding: 3em 0;
  }

  .who-slider{
    width: 70em;
    margin-top: 4em;
  }

  .swiper-button{
    width: 4em;
    height: 4em;
    svg{
      width: .6875em;
    }
    &-prev{
      left: -5.125em;
    }
    &-next{
      right: -5.125em;
    }
  }

  .who-card{
    &:before{
      top: 8em;
      width: 12.3125em;
      left: 0;
      transform: rotate(-180deg);
    }
    &:after{
      left: 8.25em;
      width: 1px;
    }
    &__content{
      padding-left: 2em;
    }
    &__top{
      padding: 2em 0;
    }
    &__num{
      font-size: 3.5em;
    }
    &__bottom{
      padding: 0 0 4em 5em;
      margin-top: -1.19em;
    }
    &__info{
      margin-top: 2.25em;
    }
    &__subtitle{
      font-size: 2.5em;
    }
    &__desc{
      font-size: 1.5em;
      margin-top: 1em;
    }
    &__img{
      padding-bottom: 92%;
    }
    &__content2{
      .who-card__info{
        padding: 4.25em 3.75em;
      }
    }
    &__title{
      font-size: 3.5em;
    }
    &__desc2{
      max-width: 20em;
      .text40{
        font-size: 2em;
      }
    }
    &__title2{
      font-size: 3em;
    }
    &__title3{
      font-size: 2em;
    }
  }
}


@media screen and (max-width: 1280px){
  .main{
    padding: 5.125em 0;
    &__bg{
      top: 0;
    }
    &__title{
      font-size: 8.75em;
    }
    &__img{
      width: 54.1875em;
      ._view2{
        display: none;
      }
      ._view3{
        display: block;
      }
    }
  }

  .main-offer{
    display: block;
    padding: 1.37em 2.5em 2.5em;
    width: 53.9375em;
    &__info{
      gap: .375em;
    }
    &__desc{
      font-size: 1.5em;
    }
    &__btn{
      width: 100%;
      margin: 2.125em 0 0;
    }
  }

  .for-who{
    padding-top: 6em;
  }

  .swiper-button{
    &-prev{
      left: -1.25em;
    }
    &-next{
      right: -1.25em;
    }
  }

  .who-card{
    grid-template-columns: 31em calc(100% - 31em);
    &__content2{
      .who-card__info{
        padding-bottom: 2.5em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 7.44em 0 5em;
    &__title{
      font-size: 7.5em;
      line-height: 75%;
    }
  }

  .main-offer{
    padding: 1.5em 2.75em 2.39em;
    border-radius: .5em;
    &__info{
      gap: .5em;
    }
    &__title{
      font-size: 2em;
    }
    &__desc{
      font-size: 1.25em;
    }
    &__btn{
      height: 5em;
      margin-top: 1.5em;
      border-radius: .625em;
      span{
        font-size: 1.5em;
      }
    }
  }

  .for-who{
    padding: 4.5em 0 2.25em;
  }

  .who-slider{
    margin-top: 2em;
    .swiper{
      &-container{
        width: 100%;
      }
      &-slide{
        width: 20.75em;
        padding-right: .75em;
      }
    }
  }

  .swiper-button{
    &-prev{
      left: 0;
    }
    &-next{
      right: 0;
    }
  }

  .who-card{
    display: flex;
    flex-direction: column;
    border-radius: 0.625em;
    &:after{
      left: 5.16em;
      height: calc(100% - 18em);
    }
    &:before{
      top: 4.375em;
      width: calc(100% - 2.5em);
      left: 50%;
      transform: translateX(-50%) rotate(-180deg);
    }
    &__content{
      padding: 0;
    }
    &__top{
      padding: 1em 1.25em;
    }
    &__num{
      font-size: 2.5em;
    }
    &__bottom{
      padding: 1.44em 2.75em 2em;
    }
    &__content2{
      margin-top: auto;
      height: unset;
      .who-card__info{
        padding: 2em 3.81em;
      }
    }
    &__name{
      font-size: 3em;
    }
    &__subtitle{
      font-size: 1.5em;
    }
    &__desc{
      font-size: 1em;
      margin-top: .75em;
    }
    &__img{
      border-radius: 0 0 .625em .625em;
      min-height: unset;
    }
    &__price{
      font-size: 1.25em;
    }
    &__title,
    &__title2{
      font-size: 2em;
      margin: 0;
    }
    &__desc2{
      max-width: 12em;
      margin-top: 1em;
      .text40{
        font-size: 1.25em;
      }
    }
    &__title3{
      font-size: 1.5em;
    }
    .text58{
      font-size: 1.75em;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    padding: 4.75em 0 7.5em;
    &__title{
      font-size: 3.58em;
      line-height: 84%;
    }
    &__img{
      margin-top: -1em;
      ._view3{
        display: none;
      }
      ._view4{
        display: block;
      }
    }
  }

  .main-offer{
    padding: .875em 1em 1.5em;
    text-align: center;
    &__info{
      gap: .69em;
    }
    &__title{
      font-size: 1.5em;
    }
    &__desc{
      font-size: .875em;
    }
    &__btn{
      height: 3.625em;
      border-radius: .47em;
      span{
        font-size: 1em;
      }
    }
  }

  .who-slider{
    width: calc(100% - 1.875em);
    margin-left: auto;
    margin-right: auto;
    .swiper{
      &-container{
        width: calc(100% + .75em);
      }
      &-slide{
        width: 100%;
      }
    }
  }

  .swiper-button{
    width: 2em;
    height: 2em;
    &-prev{
      left: -.9375em;
    }
    &-next{
      right: -.9375em;
    }
    svg{
      width: .375em;
    }
  }

  .who-card{
    &:before{
      top: 3.5em;
    }
    &__num{
      font-size: 2em;
    }
    &__bottom{
      padding: .81em 3.75em 1.94em;
      margin: 0;
    }
    &__info{
      margin-top: .5em;
    }
    &__name{
      font-size: 2em;
    }
    &__subtitle{
      font-size: 1.125em;
    }
    &__desc{
      margin-top: .5em;
    }
    &__title,
    &__title2{
      font-size: 1.75em;
    }
    .text58{
      font-size: 1.5em;
    }
  }
}
