.header{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 2;
  padding-top: 2.31em;
  &__inner{
    display: flex;
  }
  &__date{
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    padding: 0 1em;
    height: 5.3125em;
    @include center;
    min-width: 19.6875em;
    background-color: #fff;
    border-radius: 0 0 1.375em 1.375em;
    color: $black;
    text-transform: uppercase;
    font-weight: 700;
    span{
      font-size: 1.75em;
      line-height: 115%;
    }
  }
  &__btn{
    margin-left: auto;
    @include center;
    padding: 0 .97em;
    height: 2.125rem;
    border-radius: 0.25em;
    border: 1px solid $primary;
    background: rgba(0, 255, 255, 0.15);
    color: $primary;
    &:hover{
      background: rgba(0, 255, 255, 1);
      color: $black;
    }
    span{
      font-size: 1.125em;
      line-height: 115%;
    }
  }
}


@media screen and (max-width: 1588px){
  .header{
    padding-top: 2em;
    &__date{
      height: 4.25em;
      min-width: 15.25em;
      span{
        font-size: 1.32em;
      }
    }
  }
}


@media screen and (max-width: 1280px){
  .header{
    padding-top: 1.81em;
    &__date{
      height: 4em;
      min-width: 14.5em;
      padding: 0 1.25em;
      border-radius: 0 0 1em 1em;
      span{
        font-size: 1.25em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding-top: 1.44em;
    &__btn{
      padding: 0 1.41em;
      height: 2.0625em;
      span{
        font-size: 1em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    padding-top: .625em;
    &__date{
      height: 2em;
      min-width: 7.75em;
      padding: 0 1em;
      border-radius: 0 0 .625em .625em;
      span{
        font-size: .625em;
      }
    }
    &__btn{
      padding: 0 .625em;
      height: 1.375em;
      span{
        font-size: .72em;
      }
    }
  }
}
