.success-stories{
  padding: 6.25em 0;
  &__items{
    width: 83.06em;
    max-width: 100%;
    margin: 5em auto 0;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
    gap: 1.25em;
  }
}

.student-card{
  border-radius: 1.375em;
  background: rgba(217, 217, 217, 0.11);
  border: 1px solid rgba($primary,.5);
  overflow: hidden;
  &__top{
    position: relative;
  }
  &__play{
    @include centerAbsolute;
    z-index: 2;
    width: 5em;
  }
  &__img{
    border-radius: 1.375em 1.375em 0 0;
    @include imgCover(73.4%);
  }
  &__bottom{
    height: 5.06em;
    @include center;
    text-align: center;
    position: relative;
    overflow: hidden;
    &:before{
      content: '';
      @include centerAbsolute;
      width: 28.25em;
      height: 15.375em;
      border-radius: 28.25em;
      opacity: 0.54;
      background: radial-gradient(497.44% 147.35% at 0% 58.13%, #0FF 20.71%, #6141D4 100%);
      filter: blur(143.6999969482422px);
    }
  }
  &__title{
    position: relative;
    z-index: 2;
  }
}

.tariffs{
  padding: 6.25em 0;
  background-image: url(../img/bubles.webp);
  background-repeat: no-repeat;
  background-position: center top;
  background-size: 84.375em auto;
  &__wrap{
    overflow: hidden;
  }
  &__timer{
    display: flex;
    justify-content: center;
    margin-top: 2.19em;
  }
}

.timer{
  @include center;
  min-width: 40em;
  padding: 2.81em 4.37em 3.19em;
  &:before{
    border-radius: 1.375rem;
    border: 1px solid #FFF;
    background: radial-gradient(164.96% 127.84% at 50% -19.59%, rgba(0, 224, 255, 0.25) 0%, rgba(37, 130, 216, 0.00) 62.48%), linear-gradient(0deg, rgba(0, 2, 17, 0.49) 0%, rgba(0, 2, 17, 0.49) 100%), rgba(217, 217, 217, 0.11);
    backdrop-filter: blur(12.850000381469727px);
  }
  .countdown{
    position: relative;
    z-index: 2;
    span{
      @include sameNumWidth;
      color: #fff;
    }
    b{
      text-shadow: 0px 4px 4px rgba($primary, 0.30);
      color: $primary;
    }
  }
  .countdown{

  }
}

.tariffs-slider{
  margin-top: 5.12em;
  position: relative;
  .swiper{
    &-container{
      width: 100%;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      width: 34em;
      padding-right: 1.875em;
    }
  }
}

.tariff-card{
  height: 100%;
  border-radius: 1.375em;
  overflow: hidden;
  background-color: rgba(217, 217, 217, 0.11);
  border: 1px solid rgba(#fff,.2);
  position: relative;
  display: flex;
  flex-direction: column;
  &:before{
    content: '';
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 60%;
    width: 16.25em;
    max-width: 100%;
    height: 16.25em;
    border-radius: 50%;
    opacity: 0.7;
    background: $primary;
    filter: blur(175px);
    pointer-events: none;
  }
  &__top{
    position: relative;
    flex-shrink: 0;
    border-bottom: 1px solid rgba($primary,.5);
  }
  &__img{
    @include imgCover(71%);
    &:before{
      background: linear-gradient(0deg, #000 0%, rgba(0, 0, 0, 0.00) 61.64%);
    }
  }
  &__name{
    z-index: 2;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    min-height: 12.25em;
    padding: 2.375em 3em;
    text-align: center;
    font-weight: 700;
    color: $primary;
    span{
      font-size: 3.25em;
      line-height: 115%;
    }
  }
  &__content{
    padding: 3.25em 1.5em 1.5em;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    position: relative;
    z-index: 2;
  }
  &__info{
    padding: 0 .5em 2em;
  }
  &__title{
    text-transform: uppercase;
    display: flex;
    align-items: center;
  }
  &__items{
    margin-top: 2em;
    @include grid100gap(1.5em);
  }
  &__bottom{
    margin-top: auto;
  }
  &__nav{
    margin-top: 2.81em;
    @include grid100gap(1em);
    ._gray{
      border: 1px solid #FFF;
    }
  }
  &__bonus{
    margin-top: 2.25em;
    height: 5.875em;
    @include center;
    border-radius: 1.375em;
    border: 1px solid $primary;
    background: linear-gradient(180deg, rgba(97, 65, 212, 0.21) 0%, rgba(97, 65, 212, 0.40) 60.52%, rgba(97, 65, 212, 0.40) 100%), linear-gradient(180deg, rgba(0, 255, 255, 0.40) 0%, rgba(0, 255, 255, 0.16) 100%), #0F122F;
    font-weight: 700;
    text-transform: uppercase;
    position: relative;
    overflow: hidden;
    ._ellipse{
      @include centerAbsolute;
      border-radius: 24.625em;
      background: radial-gradient(50% 50% at 50% 50%, #6141D4 38.65%, rgba(97, 65, 212, 0.00) 100%);
      width: 24.625em;
      height: 7.75em;
      filter: blur(50px);
    }
    &-title,
    &-title2{
      position: relative;
      z-index: 2;
    }
    &-title{
      font-size: 2.625em;
      margin-right: .125em;
    }
    &-title2{
      font-size: 2em;
      line-height: 115%;
    }
  }
}

.tariff-item,
.plus-item,
.payment-item{
  &__icon{
    width: 1.75em;
    height: 1.75em;
    margin-right: 1.25em;
    flex-shrink: 0;
    position: relative;
    img{
      width: 100%;
      display: block;
    }
    &:before{
      content: '';
      @include centerAbsolute;
      width: 3.125em;
      height: 3.125em;
      border-radius: 50%;
      opacity: 0.5;
      background: radial-gradient(50% 50% at 50% 50%, #0FF 0%, rgba(97, 65, 212, 0.00) 100%);
    }
  }
}

.tariff-item{
  display: flex;
  &__text{
    font-size: 1.5em;
    line-height: 115%;
    span{
      font-weight: 700;
      color: $primary;
    }
  }
}

.tariff-prices{
  text-align: center;
  &__text{
    font-size: 1.625em;
    line-height: 115%;
    color: $primary;
  }
  &__num1{
    margin-top: 1.06em;
    font-weight: 600;
    span{
      text-decoration: line-through;
      font-size: 2.5em;
      line-height: 115%;
    }
  }
  &__num2{
    margin-top: .375em;
    font-weight: 700;
    span{
      font-size: 4.375em;
    }
  }
}


@media screen and (max-width: 1588px){
  .tariffs{
    padding: 3em 0;
    &__timer{
      margin-top: 2em;
    }
  }

  .timer{
    padding: 2em 3.79em 2.44em;
    min-width: 33em;
  }

  .tariffs-slider{
    margin-top: 3.56em;
    .swiper{
      &-slide{
        width: 35.125em;
        padding-right: 1em;
      }
    }
  }
}


@media screen and (max-width: 1280px){
  .success-stories{
    padding: 3em 0;
    &__items{
      margin-top: 1.75em;
      grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
      gap: 1em;
    }
  }

  .student-card{
    &__bottom{
      height: 3.94em;
    }
    &__title{
      font-size: 1.75em;
    }
    &__play{
      width: 4.5em;
    }
  }

  .tariffs{
    &__timer{
      margin-top: 3em;
    }
  }

  .tariffs-slider{
    margin-top: 2.44em;
    .swiper{
      &-slide{
        width: 22.125em;
        padding-right: 1.875em;
      }
    }
  }

  .tariff-card{
    &__img{
      padding-bottom: 77.5%;
    }
    &__name{
      min-height: 8.625em;
      padding: 2em 1em;
      span{
        font-size: 2em;
      }
    }
    &__content{
      padding: 1.25em 1.125em;
    }
    &__info{
      padding: 0 0 1.5em;
    }
    &__title{
      .text32{
        font-size: 1.25em;
      }
      .program-item__btn-icon{
        width: 2em;
        margin-left: .44em;
      }
    }
    &__items{
      margin-top: 1.19em;
    }
    &__nav{
      margin-top: 1.31em;
      padding: 0 .875em;
    }
    &__btn{
      height: 3.625em;
      border-radius: .5em;
      span{
        font-size: 1em;
      }
    }
    &__bonus{
      height: 3.125em;
      border-radius: .625em;
      &-title{
        font-size: 1.5em;
      }
      &-title2{
        font-size: 1em;
      }
    }
  }

  .tariff-item{
    &__icon{
      width: 1.5em;
      height: 1.5em;
      margin-right: .625em;
      &:before{
        width: 2.625em;
        height: 2.625em;
      }
    }
    &__text{
      font-size: 1em;
    }
  }

  .tariff-prices{
    &__text{
      font-size: 1.25em;
    }
    &__num1{
      margin-top: .375em;
      span{
        font-size: 2em;
      }
    }
    &__num2{
      margin-top: .5em;
      span{
        font-size: 3.25em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .success-stories{
    padding: 2.25em 0;
    &__items{
      margin-top: 2em;
      grid-template-columns: repeat(3,calc(100% / 3 - .5em));
      gap: .75em;
    }
  }

  .student-card{
    &__bottom{
      height: 3.56em;
    }
    &__title{
      font-size: 1.5em;
    }
    &__play{
      width: 3.5em;
    }
  }

  .tariffs{
    background-size: 100% auto;
    padding: 2.25em 0;
    &__timer{
      margin-top: 1.25em;
    }
  }

  .timer{
    padding: 1.625em 2.69em 2.25em;
    min-width: 25em;
  }

  .tariffs-slider{
    margin-top: 2.45em;
    .swiper{
      &-container{
        width: 19.1875em;
      }
      &-slide{
        width: 100%;
        padding-right: .75em;
      }
    }
  }

  .tariff-card{
    &__img{
      padding-bottom: 92.2%;
    }
    &__title{
      .text32{
        font-size: 1.125em;
      }
      .program-item__btn-icon{
        width: 1.5em;
      }
    }
    &__nav{
      padding: 0;
    }
  }

  .tariff-item{
    &__icon{
      margin-right: 1em;
    }
    &__text{
      font-size: .875em;
    }
  }

  .tariff-prices{
    &__text{
      font-size: 1.125em;
    }
  }
}


@media screen and (max-width: 576px){
  .success-stories{
    &__items{
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
    }
  }

  .student-card{
    border-radius: 0.75em;
    &__img{
      border-radius: .75em .75em 0 0;
    }
    &__bottom{
      height: 2em;
    }
    &__title{
      font-size: 1.125em;
    }
    &__play{
      width: 2.375em;
    }
  }

  .tariffs{
    &__timer{
      margin-top: 1.5em;
    }
  }

  .timer{
    padding: 1.13em 1.75em 1.5em;
    min-width: 16em;
  }

  .tariffs-slider{
    margin-top: 2em;
    .swiper-button{
      &-prev{
        left: 0;
      }
      &-next{
        right: 0;
      }
    }
  }
}
