.h1{
  font-size: 6.25em;
  line-height: 90%;
  font-weight: 700;
  font-family: $font2;
  text-transform: uppercase;
  span{
    color: $primary;
  }
}

.text80{
  font-size: 5em;
  line-height: 115%;
}

.text70{
  font-size: 4.375em;
  line-height: 115%;
}

.text60{
  font-size: 3.75em;
  line-height: 115%;
}

.text58{
  font-size: 3.625em;
  line-height: 115%;
}

.text50{
  font-size: 3.125em;
  line-height: 115%;
}

.text42{
  font-size: 2.625em;
  line-height: 115%;
}

.text40{
  font-size: 2.5em;
  line-height: 115%;
}

.text32{
  font-size: 2em;
  line-height: 115%;
}

.text24{
  font-size: 1.5em;
  line-height: 115%;
}

.text20{
  font-size: 1.25em;
  line-height: 120%;
}

.text19{
  font-size: 1.1875em;
  line-height: 120%;
}

.text18{
  font-size: 1.125em;
  line-height: 120%;
}

.text17{
  font-size: 1.0625em;
  line-height: 120%;
}

.text16{
  font-size: 1em;
