.footer{
  padding: 1em 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    font-size: 3.44em;
    line-height: 115%;
    font-weight: 700;
    font-family: $font2;
    span{
      color: $primary;
    }
  }
  &__conf{
    margin-top: .875em;
    color: #D1D1D1;
    max-width: 16.5em;
    font-family: $font3;
    span{
      font-size: .875em;
      line-height: 125%;
    }
  }
  &__links{
    margin-top: 2.44em;
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  &__link{
    @include lastM(0 2.38em 0 0);
    span{
      font-size: 1em;
      line-height: 115%;
    }
  }
}

.socials{
  display: flex;
  justify-content: flex-end;
  align-items: center;
  &__item{
    display: flex;
    align-items: center;
    color: $primary;
    @include lastM(0 3.875em 0 0);
    &:hover{
      color: $primary;
      opacity: .8;
    }
    &-icon{
      width: 1.5em;
      margin-right: .75em;
      flex-shrink: 0;
      position: relative;
    }
    img{
      width: 100%;
      position: relative;
      z-index: 2;
      display: block;
    }
    &-title{
      font-size: 1.125em;
      line-height: 115%;
    }
  }
}


@media screen and (max-width: 1588px){
  .footer{
    padding: 2.625em 0;
    &__inner{
      align-items: unset;
    }
  }
}


@media screen and (max-width: 1280px){
  .footer{
    padding: 2em 0;
    &__title{
      font-size: 3.75em;
    }
    &__conf{
      max-width: 23.875em;
    }
    &__nav{
      display: flex;
      align-items: center;
    }
    &__links{
      flex-shrink: 0;
      margin: 0 0 0 2.5em;
      flex-direction: column;
      align-items: flex-start;
    }
    &__link{
      @include lastM(0 0 .9375em);
    }
  }

  .socials{
    justify-content: unset;
    align-items: flex-start;
    flex-direction: column;
    &__item{
      @include lastM(0 0 1.125em);
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 1.44em 0;
    &__title{
      font-size: 2.45em;
    }
    &__conf{
      margin-top: .75em;
      max-width: 14.25em;
      span{
        font-size: .75em;
      }
    }
    &__nav{
      margin-left: 2em;
    }
  }

  .socials{
    &__item{
      span{
        font-size: 1em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    padding: .94em 0;
    &__inner{
      flex-direction: column;
      align-items: flex-start;
    }
    &__title{
      font-size: 2.85em;
    }
    &__conf{
      margin-top: .5em;
    }
    &__nav{
      order: -1;
      margin: 0 0 1.25em;
      display: block;
    }
    &__links{
      margin: 1.5em 0 0;
    }
    &__link{
      @include lastM(0 0 .5em);
    }
  }

  .socials{
    &__item{
      @include lastM(0 0 1em);
    }
  }
}
