.speakers{
  padding: 6.25em 0;
  position: relative;
  background-image: url(../img/speaker-bg.webp);
  background-size: 50.25em auto;
  background-repeat: no-repeat;
  background-position: right 10.5625em top 8.5em;
  .h1{
    text-align: center;
  }
  &__items{
    display: flex;
    justify-content: center;
  }
}

.speaker-card{
  margin: 0 1.0625em;
  width: 31.75em;
  flex-shrink: 0;
  padding-top: 21.875em;
  position: relative;
  &._type2{
    .speaker-card__img{
      width: 42.75em;
      bottom: -8em;
      left: -5.5em;
    }
  }
  &__img{
    position: absolute;
    bottom: -11em;
    left: -18.5em;
    width: 67.6875em;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &__content{
    background-color: rgba(#000,.7);
    border: 1px solid rgba($primary,.7);
    border-radius: 1.375em;
    position: relative;
    z-index: 2;
  }
  &__top{
    border-radius: 1.375em 1.375em 0 0;
    padding: 1.5em;
    background-image: url(../img/bg2.webp);
    background-size: cover;
    background-position: center;
    @include center;
    text-align: center;
    position: relative;
    overflow: hidden;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 92.5rem;
      height: 15.375rem;
      border-radius: 92.5em;
      background: radial-gradient(177.84% 63.18% at 48.72% 47.97%, #6141D4 0%, rgba(3, 255, 255, 0.00) 100%);
      filter: blur(116.9000015258789px);
      z-index: 2;
    }
  }
  &__bottom{
    background-color: rgba($primary,.2);
    padding: 2em;
    border-radius: 0 0 1.375em 1.375em;
  }
  &__list{
    @include grid100gap(1.5em);
    li{
      font-size: 1.25em;
      line-height: 110%;
      position: relative;
      padding-left: .8em;
      &:before{
        border-radius: 50%;
        background: #FFF;
        box-shadow: 0px 0px 9.9px 0px #FFF;
        width: .3em;
        height: .3em;
        position: absolute;
        content: '';
        left: 0;
        top: .4em;
      }
    }
    span{
      color: $primary;
      font-weight: 700;
    }
  }
}

._rect{
  pointer-events: none;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 1.375em;
  opacity: 0.2;
  background: #0FF;
  filter: blur(100px);
  width: 68.5em;
  height: 37.625em;
  max-width: 100%;
  z-index: -1;
}

.programs{
  padding: 6.25em 0;
  position: relative;
  ._ellipse{
    top: unset;
    bottom: 0;
    width: 64em;
    height: 27.0625em;
    z-index: -1;
    background: #627EE9;
    filter: blur(263.3999938964844px);
  }
  &__nav{
    margin-top: 3.5em;
    display: flex;
    justify-content: center;
  }
  &__btn{
    padding: 0 2.75em;
    min-width: 46.25em;
    ._view2{
      display: none;
    }
    &._active{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
  .container{
    position: relative;
    z-index: 2;
  }
}

.program-item{
  margin-top: 5em;
  display: grid;
  grid-template-columns: 47.19em calc(100% - 47.19em);
  border: 1px solid rgba(#fff,.5);
  border-radius: 1.375em;
  overflow: hidden;
  background: rgba(217, 217, 217, 0.11);
  &._hide{
    display: none;
  }
  &._active{
    .program-item__btn-icon{
      transform: rotate(-90deg);
    }
  }
  &._type2{
    .program-item__top{
      &:before{
        background: linear-gradient(270deg, #6141D4 -2.49%, rgba(97, 65, 212, 0.00) 51.87%);
      }
    }
  }
  &__info{
    padding-bottom: 3.56em;
  }
  &__top{
    padding: 2.44em 1em 1.31em 3.125em;
    display: flex;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      bottom: 0;
      transform: rotate(-180deg);
      left: 0;
      width: 24.625em;
      height: 1px;
      opacity: .4;
      background: linear-gradient(270deg,rgba(0,255,255,0) -2.49%,#0ff 2.42%,rgba(0,255,255,0) 51.87%);
    }
  }
  &__title{
    padding: 1.38em 0 0 2.94em;
  }
  &__nav{
    padding: 3.25em 1em 0 12.125em;
    display: flex;
  }
  &__btn{
    display: flex;
    align-items: center;
    color: $secondary;
    font-weight: 800;
    &-title{
      font-size: 1.5em;
      line-height: 115%;
    }
    &-icon{
      margin-left: 1em;
      width: 2em;
      flex-shrink: 0;
      position: relative;
      transition: all .3s;
      &:before,
      &:after{
        content: '';
        @include centerAbsolute;
        border-radius: 50%;
        transition: inherit;
        opacity: 0.5;
      }
      &:before{
        background: $secondary;
        opacity: 0.5;
        filter: blur(8.893758773803711px);
        z-index: -1;
        width: 100%;
        height: 100%;
      }
      &:after{
        width: 125%;
        height: 125%;
        border: 1px solid $secondary;
        filter: blur(0.8999999761581421px);
        pointer-events: none;
        opacity: 0;
      }
      img{
        display: block;
        width: 100%;
      }
    }
  }
  &__gift{
    width: 2.6875em;
    align-self: flex-start;
  }
}

.program-result{
  padding: 3.8em 4.38em 3.8em 5.94em;
  &:before{
    background: linear-gradient(180deg, #0FF 0%, rgba(0, 255, 255, 0.00) 100%), #6141D4;
    opacity: 0.8;
  }
  &__title,
  &__desc{
    position: relative;
    z-index: 2;
  }
  &__desc{
    margin-top: 1.375em;
  }
}

.program-detail{
  display: none;
  grid-column: 1/-1;
  background-color: rgba(#000,.52);
  padding: 2.625em 12.12em 4.5em;
  &__top{
    display: flex;
    align-items: center;
  }
  &__title{
    color: $secondary;
  }
  &__time{
    font-size: 1.75em;
    line-height: 115%;
    margin-left: .25em;
  }
  &__items{
    @include grid100gap(1em);
    margin-top: 3.38em;
    max-width: 52.38em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      bottom: 100%;
      left: calc(1.375em / 2 - .5px);
      height: 2.4375em;
      width: 1px;
      background-color: $secondary;
      margin-bottom: .5em;
    }
  }
  &__item{
    padding-left: 2.37em;
    position: relative;
    font-weight: 500;
    &._active{
      color: $secondary;
      &:after{
        opacity: 1;
      }
    }
    &:after{
      width: 1.375em;
      height: 1.375em;
      border: 1px solid #b2ff51;
      filter: blur(2px);
      pointer-events: none;
      opacity: 0;
      top: .3125em;
      left: 0;
    }
    &:after,
    &:before {
      content: '';
      position: absolute;
      border-radius: 50%;
    }
    &:before{
      top: .81em;
      left: .5em;
      width: 0.375em;
      height: 0.375em;
      background-color: #fff;
      box-shadow: 0 0 9.9px 0 #fff;
    }
    &-text{
      font-size: 1.625em;
      line-height: 120%;
    }
    span{
      color: $secondary;
      font-weight: 600;
    }
  }
}


@media screen and (max-width: 1588px){
  .speakers{
    background-position: right 0 top 0;
  }

  .programs{

  }

  .program-item{
    margin-top: 4em;
    grid-template-columns: 36.44em calc(100% - 36.44em);
    &__info{
      padding-bottom: 2.81em;
    }
    &__top{
      padding: 1.5em 2em 1em;
      &:before{
        width: 15.5625em;
      }
    }
    &__num{
      font-size: 2.5em;
    }
    &__title{
      padding: 0 0 0 .94em;
    }
    &__nav{
      padding: 1.94em 1em 0 6em;
    }
    &__btn{
      &-title{
        font-size: 1.75em;
      }
      &-icon{
        margin-left: .75em;
        width: 1.5em;
      }
    }
  }

  .program-result{
    padding: 1.875em 3em;
    &__desc{
      margin-top: 2em;
      .text24{
        font-size: 1.125em;
      }
    }
  }

  .program-detail{
    padding: 1.81em 14.87em 3.5em;
    &__title{
      font-size: 1.25em;
    }
    &__time{
      font-size: 1.25em;
      margin-left: .75em;
    }
    &__items{
      margin-top: 2.19em;
      max-width: 59.7em;
      &:before{
        height: 1.3125em;
      }
    }
    &__item{
      padding-left: 1.81em;
      &:before{
        top: .62em;
      }
      &:after{
        display: none;
      }
      &-text{
        font-size: 1.125em;
      }
    }
  }
}


@media screen and (max-width: 1280px){
  .speakers{
    padding: 3em 0;
    background-position: right 5.1875em top 4.125em;
    background-size: 23.375em auto;
  }

  .speaker-card{
    width: 24.75em;
    padding-top: 19.88em;
    &__list{
      br{
        display: none;
      }
    }
    &._type2{
      .speaker-card__img{
        width: 25.125em;
        bottom: 5em;
        left: 0em;
      }
    }
    &__img{
      width: 30.75em;
      bottom: 2em;
      left: -5em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .programs{
    padding: 3em 0;
  }

  .program-item{
    &__info{
      padding-bottom: 2.81em;
    }
    &__nav{
      padding-top: 2.94em;
    }
  }

  .program-result{
    padding: 1.5em 2em;
    &__desc{
      margin-top: 1.5em;
    }
  }

  .program-detail{
    padding: 1.75em 6em 3.75em;
  }
}


@media screen and (max-width: 992px){
  .speaker-card{
    font-size: .75em;
  }

  .programs{
    &__nav{
      margin-top: 2.5em;
    }
    &__btn{
      padding: 0 1.375em;
      min-width: 33.875em;
      height: 5em;
      border-radius: .65em;
      span{
        font-size: 1.5em;
      }
    }
  }

  .program-item{
    grid-template-columns: 100%;
    &__info{
      padding-bottom: 1.75em;
    }
    &__top{
      padding: 1.5em 2.5em .94em;
    }
    &__num,
    &__title .text40{
      font-size: 2em;
    }
    &__title{
      padding-left: 1.06em;
    }
    &__nav{
      padding: 1em 1em 0 6em;
    }
    &__btn{
      &-title{
        font-size: 1.25em;
      }
      &-icon{
        margin-left: 1em;
      }
    }
    &__gift{
      width: 2.25em;
    }
  }

  .program-result{
    padding: 1.5em 2.5em;
    order: 2;
    &__title{
      .text40{
        font-size: 2em;
      }
    }
    &__desc{
      margin-top: .69em;
    }
  }

  .program-detail{
    padding: 1.69em 6em;
    &__item{
      &:before{
        top: .375em;
      }
      &-text{
        font-size: 1em;
      }
    }
    &__title,
    &__time{
      font-size: 1.125em;
    }
  }
}


@media screen and (max-width: 576px){
  .speakers{
    &__items{
      margin-top: 3em;
      display: grid;
      grid-template-columns: 100%;
      gap: 1.34em;
    }
  }

  .speaker-card{
    margin: 0;
    width: 100%;
    &__img{
      left: -3em;
    }
  }

  .programs{
    .h1{
      padding: 0 1em;
    }
    &__nav{
      margin-top: 2em;
    }
    &__btn{
      min-width: unset;
      width: 100%;
      height: 3.625em;
      padding: 0 3em;
      span{
        font-size: 1em;
        line-height: 115%;
      }
    }
  }

  .program-item{
    margin-top: 2em;
    &__info{
      padding-bottom: 2.375em;
    }
    &__top{
      padding: 1.5em 1.5em 1em;
      @include grid100gap(1em);
    }
    &__num,
    &__title .text40{
      font-size: 1.5em;
    }
    &__title{
      padding-left: 0;
    }
    &__nav{
      padding: 1em 1.5em 0;
    }
    &__btn{
      &-title{
        font-size: 1em;
      }
      &-icon{
        margin-left: .5em;
      }
    }
    &__gift{
      width: 1.85em;
    }
  }

  .program-result{
    padding: 1.5em 1.5em 1.625em;
    &__title{
      .text40{
        font-size: 1.5em;
      }
    }
    &__desc{
      margin-top: 1em;
      .text24{
        font-size: 1em;
      }
    }
  }

  .program-detail{
    padding: 1.5em;
    &__top{
      @include grid100gap(.5em);
    }
    &__time{
      margin: 0;
    }
  }
}
