@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    height: 7.75em;
    border-radius: 1.375em;
    text-transform: uppercase;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 2.1em;
        line-height: 115%;
    }
}

.m-btn-blue{
    background-image: url(../img/btn-bg.webp);
    background-size: cover;
    background-position: center;
    font-weight: 700;
    color: $black;
    position: relative;
    &:hover{
        color: $black;
        opacity: .8;
    }
    &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-radius: inherit;
        background: rgba(0, 255, 255, 0.59);
        filter: blur(10.399999618530273px);
        z-index: -1;
    }
    &:after{
        content: '';
        @include centerAbsolute;
        width: calc(100% + 2em);
        height: calc(100% + 2.25em);
        border-radius: 33.875em;
        background: radial-gradient(50% 50% at 50% 50%, #03FFFF 38.65%, rgba(3, 255, 255, 0.00) 100%);
        filter: blur(175px);
        pointer-events: none;
    }
    span{
        text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.30);
        position: relative;
        z-index: 2;
    }
    &._gray{
        background-image: url(../img/btn-bg2.webp);
        color: #fff;
        &:after{
            height: 100%;
            width: 24.625em;
            max-width: 100%;
        }
        &:before{
            display: none;
        }
    }
}
